	module.exports = {"data":{"markdownRemark":{"html":"<h2>GUI Options for Git</h2>\n<p>Most people prefer to use Git as a <a href=\"https://en.wikipedia.org/wiki/Command-line_interface\">CLI (command-line interface)</a> tool, but there are plenty of <a href=\"https://en.wikipedia.org/wiki/Graphical_user_interface\">GUI (graphical user interface)</a> tools allowing you to use Git in a graphical way. GUI tools can useful to get a better view of the state of your Git repository, and some might find them easier to use than the CLI. However, knowing how to use the CLI is highly recommended because it will be the same interface on every platform, because it is generally considered to be more efficient, and because it is required as soon as you want to do something even a little bit complex.</p>\n<h2>List of Git GUI Based Solutions</h2>\n<ul>\n<li><a href=\"https://www.gitkraken.com\">GitKraken</a> is a popular Git GUI for Windows, Mac and Linux. It is proprietary but free for non-commercial use.</li>\n<li><a href=\"https://desktop.github.com/\">GitHub Desktop</a> is the Git client application provided by GitHub, allowing better integration with GitHub than other solutions. It is available for Windows and Mac, but not yet for Linux. It is free and open source.</li>\n<li><a href=\"https://www.sourcetreeapp.com/\">SourceTree</a> is another Git GUI for Windows and Mac by Atlassian. It has many features such as interactive rebase, designed to make using Git easier for beginners.</li>\n<li><a href=\"https://www.git-tower.com/mac/\">Git Tower</a> is available for Mac and Windows.</li>\n<li><a href=\"https://tortoisegit.org/\">TortoiseGit</a> is a Windows Shell Interface to Git based on TortoiseSVN. It’s open source and can be built with freely available software.</li>\n<li><a href=\"https://www.syntevo.com/smartgit/\">SmartGit</a> is a Git client free for a non-commercial use for Windows, Mac and Linux.</li>\n</ul>","fields":{"slug":"/git/GUI-options/"},"frontmatter":{"title":"GUI Options"}}},"pathContext":{"slug":"/git/GUI-options/"}}

